

.parent {
	width: 100%;
	margin: 0 auto;
	max-width: 600pt;
	border-radius: 5pt;
	padding: 10pt;
	background: #171b23;
}

#margin{
	height: 20pt;
	width: 100%;
}

html, body, #root{
	font-family: sans-serif;
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
	color: #FFF;
	background: #0d1117;
}

.icon{
	height: 15pt;
	vertical-align: middle;
}
#start{
	text-align: center;
}

video {
	width: calc(100%);
	border-radius: 4pt;
}


.tui-image-editor-header-logo{
	display:none;
}

.tui-image-editor-header-buttons {
	display:none;
}

.editor{
  display: flex;
  flex-flow: column;
  height: 100%;
}

.editor-header{
	flex: 0 1 auto;
	padding: 2.5pt;
	margin:0;

}

.editor-header .btn{
	margin: 2.5pt;
}

.export{
	float: right;
}

.direct{
	height: calc(100% - 20pt);
	flex: 1 1 auto;
}


#take{
	float: right;
	margin-right: 0 !important;
}

#capture .btn{
	margin-right: 5pt;
}

#captureButtons{
	margin-top: 0pt;
	margin-bottom: 7.5pt;

	padding-left: 0pt;
	padding-right: 0pt;

}
