.btn {
  display: inline-block;
  font-size: 10pt;
  font-weight: bold;
  color: #fff;
  font-family: sans-serif;
  text-decoration: none;
  background: #007bff;
  cursor: pointer;
  box-sizing: content-box;
  border: none;
  padding: 8pt 16pt 8pt 16pt;
  border-radius: 4pt;
}

.btn:hover {
  background: #0462c7;
  text-decoration: none;

}